:root{
  --colorPrincipal: #15181a;
  --letraPrincial: 'Akshar', sans-serif;
  --letraSecundaria:  'Open Sans', sans-serif;
  --letraTerciaria: 'Source Serif Pro', serif;
  --colorLetra: rgb(209, 70, 65);
  --ColorLetraSecundaria: lightgray
}

html{
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  background-color: var(--colorPrincipal);
}

*,*::after,*::before{
  box-sizing: inherit;
  padding: 0;
  margin: 0;
}
/*-------------------------Menu navegacion------------------------------------*/
header{
  width: 100%;
  height: 70px;
  background-color: var(--colorPrincipal);
  display: flex;
  justify-content: space-between;
  padding-left: 50px;
  padding-right: 50px;
  position: relative;
  margin-bottom: 20px;
}

.img-nav{
  width: 60px;
  height: 60px;
}

.img-nav img{
  width: 100%;
  height: 100%;
  object-fit: contain;
}

nav{
  width: 300px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

nav a{
 text-decoration: none;
 font-family: var(--letraPrincial);
 font-size: 22px;
}

.menu-hamburguesa{
  display: none !important;
  justify-content: center;
  align-items: center;
  font-size: 38px;
  cursor: pointer;
}

/*-------------------------Seccion quien soy------------------------------------*/

.contenedorG-soy{
  background-color: var(--colorPrincipal);
  color: white;
  display: flex;
  align-items: center;
  justify-content: space-around;
  min-height: 500px;
  height: calc(89.4vh - 20px);
  gap: 30px;
  padding: 30px;
}

.imagen-soy{
  width: 500px;
  height: 500px;
  background-color: var(--colorLetra);
  clip-path: circle();
   cursor: pointer;
   transition: background-color .5s;
}

.imagen-soy:hover{
  background-color: brown;
  box-shadow: 0px 0px 10px 2px rgb(255, 31, 31);
}


.imagen-soy img{
  width: 500px;
  height: 500px;
  object-fit: contain;
  object-position: 50% 10px;
  clip-path: circle();
  animation: animacionImagenSoy .5s;
  transition: filter .5s;
}

.informacion-soy{
  width: 550px;
  height: auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  color: var(--ColorLetraSecundaria);
  position: relative;
  background-color: rgb(206, 206, 206);
  z-index: 10;
  padding: 20px;
  padding-bottom: 100px;
  box-shadow: 3px 5px 10px 3px rgb(15, 15, 15);
  border-radius: 5px;
  border: 1px solid var(--colorPrincipal);
}
.imagen-soy:hover img{
  filter: drop-shadow(0px 0px 10px rgb(255, 146, 146));
}

.informacion-soy h1{
  color: var(--colorLetra);
  font-size: 40px;
  padding: 10px 15px;
  font-family: var(--letraSecundaria);
  background-color: var(--colorPrincipal);
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
}

.informacion-soy h3{
  font-size: 28px;
   padding: 10px 15px; 
  font-family: var(--letraSecundaria);
  background-color: var(--colorPrincipal);
  text-align: start;
}

.informacion-soy p{
  font-size: 22px;
  font-family: var(--letraSecundaria);
  background-color: var(--colorPrincipal);
  text-align: start;
  padding: 15px;
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
}


/*-------------------------Trabajos------------------------------------*/
.contenedorG-trabajos{
  width: 100%;
  height: auto;
  background-color: var(--colorPrincipal);
}

.contenedor-trabajo-boda{
  color:white;
  height: auto;
}

.contenedor-trabajo-boda h2{
  font-size: 52px;
  font-family: var(--letraTerciaria);
  text-shadow: 1px 1px 5px black;
  letter-spacing: 1px;
  margin-bottom: 10px;
  color: var(--ColorLetraSecundaria);
  text-align: center;
}

.trabajo-boda{
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  justify-content: center;
}

.trabajo-boda a{
  width: 300px;
  height: 300px;
}

.trabajo-boda a .preVistaBoda{
  width: 100%;
  height: 100%;
  display: inline-block;
  position: relative;
}

.trabajo-boda a .preVistaBoda div{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgb(0,0,0,0.5);
  opacity: 0;
  pointer-events: none;
  transition: opacity .5s;
  color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.trabajo-boda a .preVistaBoda div h3{
  font-size: 32px;
}

.trabajo-boda a:hover .preVistaBoda{
  animation: animacionPreVista 1s forwards;
}
.trabajo-boda a:hover div{
  opacity: 1;
}

.trabajo-boda a .preVistaBoda img{
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.ventanaModalContenedorG{
  position: fixed;
  top: 0;
  width: 100%;
  height: 100%;
  color: white;
  background-color: rgb(0,0,0,0.9);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 99;
}

.ventanaModalContenedor{
  width:auto;
  height:auto;
  position: relative;
}

.ventanaModalContenedor button{
  position: absolute;
  right: -40px;
  top: -40px;
  font-size: 15px;
  border-radius: 50%;
  border: 1px solid brown;
  width: 35px;
  height: 35px;
  background-color: brown;
  color: white;
  outline: none;
  cursor: pointer;
  line-height: 37px;
  transition: background-color .2s color .2s;
  z-index: 100;
  box-shadow: 0px 0px 10px 2px brown;
}

.ventanaModalContenedor button:hover{
  box-shadow: 0px 0px 10px 5px brown;
}

.imagenVentanaModal{
  max-width:800px;
  max-height: 95vh;
  object-fit:contain;
}

.fa-regular{
  font-weight: bold ; 
  font-size: 20px;
}

/*-------------------------Trabajo contenedor 1------------------------------------*/

.contenedorG-trabajo-boda1{
  width: 100%;
  min-height: 100vh;
  background-color: var(--colorPrincipal);
}
.contenedorG-trabajo-boda1 h2{
  color: white;
  font-size: 52px;
  font-family: var(--letraTerciaria);
  letter-spacing: 1px;
  text-align: center;
  margin: 15px 0px;
  color: lightgray;
  text-shadow: 1px 1px 5px black;
}

.contenedorPreVideo{
  width: 80%;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 30px;
  position: relative;
}

.contenedorPreVideo .filtroPreVideo{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgb(0,0,0,0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

.filtroPreVideo .reproductorVideo{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 300px;
    height: 300px;
    font-size: 300px;
    color: rgb(209, 70, 65,0.5);
}

 .filtroPreVideo:hover .reproductorVideo{
  color: rgb(214, 54, 48,0.9);
}


.contenedorImagenes{
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  gap: 10px;
  margin-bottom: 30px;
}

/*-------------------------Contacto------------------------------------*/

.contenedor-contacto{
  width: 100%;
  min-height: 90.4vh;
  background-color: var(--colorPrincipal);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  gap: 20px;
  padding-bottom: 10px;
}

.contenedor-contacto .contenedor-form-img{
  width: 100%;
  display: flex;
  gap: 30px;
  align-items: center;
  justify-content: space-evenly;
  flex-wrap: wrap;
}

.contenedor-form-img .form-imagen-titulo img{
  width: 400px;
  height: 400px;
  border-radius: 50%;
  object-fit: cover;
  object-position: 90% 10%;
}

.form-imagen-titulo{
  min-width: 300px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  gap: 20px;
}

.form-imagen-titulo h3{
  color: var(--ColorLetraSecundaria);
  font-size: 48px;
  text-align: center;
  font-family: var(--letraTerciaria);
  letter-spacing: 1px;
}

.contenedor-contacto form{
  display: flex;
  flex-direction: column;
  width: 600px;
  height: 550px;
  gap: 10px;
  background-color: #272829;
  box-shadow: 1px 1px 6px black;
  border-radius: 10px;
  padding: 20px;
  justify-content: space-between;
}

.contenedor-datos{
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.contenedor-contacto form label{
  display: flex;
  flex-direction: column;
  color: var(--colorLetra);
  font-size: 24px;
  font-family: var(--letraPrincial);
  position: relative;
}

.contenedor-contacto form label input{
  font-size: 22px;
  padding: 5px;
  border: none;
  outline: none;
  border-radius: 5px;
  font-family: var(--letraPrincial);
  margin-top: 8px;
  border: 2px solid transparent;
}

.contenedor-contacto form label input:focus{
  border: 2px solid var(--colorLetra);
  outline: none;
}

.contenedor-contacto form label textarea{
  resize: none;
  font-size: 20px;
  border: none;
  outline: none;
  font-family: var(--letraPrincial);
  padding: 5px;
  border-radius: 5px;
  height: 200px;
  margin-top: 8px;
  border: 2px solid transparent;
}

.contenedor-contacto form label textarea:focus{
  border: none;
  outline: none;
  border: 2px solid var(--colorLetra);
}

.contenedor-contacto form button[type = "submit"]{
  width: 200px;
  margin: 0 auto;
  font-size: 20px;
  padding: 5px;
  color: white;
  font-family: var(--letraPrincial);
  cursor: pointer;
  background-color: var(--colorLetra);
  border: none;
  outline: none;
}

.contenedor-contacto form button[type = "submit"]:hover{
  background-color: rgb(252, 58, 51);
}

.contenedor-contacto .error{
  position: absolute;
  right: 0;
  top: 3px;
  color: red;
  font-size: 19px;
  letter-spacing: 1px;
}

.redesSociales{
  font-size: 24px;
  width: 400px;
  display: flex;
  justify-content: space-around;
}

.redesSociales a{
  color: white;
}

.redesSociales a:hover{
  color: var(--colorLetra)
}

/*-------------------------media query max-width(1024px)------------------------------------*/
@media screen and (max-width: 1024px) {
  .contenedorG-soy{
    flex-direction: column;
    height: auto;
  }
  .informacion-soy{
    width: 650px;
  }
  
}

/*-------------------------media query max-width(920px)------------------------------------*/

@media screen and (max-width: 920px) {
    .contenedorG-soy{
      flex-direction: column;
      height: auto;
    }

    .ventanaModalContenedor{
      text-align: center;
    }

    .ventanaModalContenedor button{
      top: -50px;
      right: 42%;
    }
    .imagenVentanaModal{
      width: 99%;
      max-width: 600px;
    }

    .filtroPreVideo .reproductorVideo{
      width: 200px;
      height: 200px;
      font-size: 200px;
    }


}

/*-------------------------media query max-width(720px)------------------------------------*/
@media screen and (max-width: 720px) {
    header{
     padding: 0 10px;
    }
    nav{
      position: absolute;
      right: 0;
      top: 70px;
      flex-direction: column;
      justify-content: space-evenly;
      z-index: 98;
      background-color: var(--colorPrincipal);
      border-left: 2px solid var(--colorLetra);
      border-bottom: 2px solid var(--colorLetra);
      height: 200px;
      opacity: 0;
      pointer-events: none;
      width: 250px;
    }
    nav a{
      width: 80%;
      text-align: center;
      border-bottom: 1px solid var(--colorLetra);
    }

    .informacion-soy{
      width: 500px;
    }
    
    .informacion-soy h1{
      text-align: start;
      padding: 10px 15px;
    }

    .menu-hamburguesa{
      display: flex !important;
    }

    .contenedor-contacto form{
      width: 90%;
    }
    
    .contenedorPreVideo{
      width: 95%;
      height: auto;
    }

    .filtroPreVideo .reproductorVideo{
      width: 150px;
      height: 150px;
      font-size: 150px;
    }

}

/*-------------------------media query max-width(570px)------------------------------------*/

@media screen and (max-width: 570px) {

  .contenedorG-soy{
    padding: 5px;
    padding-bottom: 15px;
  }

  .imagen-soy{
    min-width: 310px;
    width: 100%;
    height: 400px;
  }
  
  .imagen-soy img{
    min-width: 310px;
    width: 100%;
    height: 400px;
    object-position: 50% 50px;
  }

  .informacion-soy{
    width: 100%;
  }
  .informacion-soy h1{
      font-size: 28px;
      padding: 10px 10px;
  }

  .informacion-soy h3{
    font-size: 22px;
    padding: 10px 10px;
  }

  .informacion-soy p{
    font-size: 19px;
      
  }

  .contenedor-contacto form{
    width: 95%;
  }

  .redesSociales{
    width: 100%;
  }

  .contenedor-contacto .form-imagen-titulo img{
    width: 310px;
    height: 310px;
  }

  .form-imagen-titulo h3{
    font-size: 40px;
  }

  .filtroPreVideo .reproductorVideo{
    width: 100px;
    height: 100px;
    font-size: 100px;
  }

}



/*-------------------------media query landscape------------------------------------*/

@media only screen  and (orientation: landscape)
and (max-width: 768px)
{
  .ventanaModalContenedor button{
    top: 45%;
    right: -50px;
  }
  .ventanaModalContenedor button{
  line-height: 30px;
  width: 31px;
  height: 31px;
  }
  
.fa-regular{
  font-weight: bold ; 
  font-size: 17px;
}
}


/*-------------------------Animaciones------------------------------------*/

@keyframes animacionImagenSoy {
  0%{
    transform: scale(1.15);
    opacity: 0;
  }
  100%{
    transform: scale(1);
    opacity: 1;
  }
}

@keyframes animacionPreVista {
  0%{
      transform: scale(1);
  }
  100%{
      transform: scale(1.05);
  }
}

/*-------------------------Clases utilitarias------------------------------------*/

.is-active{
  color: #ff7a75 !important;
}

.mostrar{
  opacity: 1;
  pointer-events: all;
  transition: opacity .25s;
}